import React, { Component } from 'react';
import Card from "./Card";
import CardGrid from "./CardGrid";

class Textures extends Component {
  render() {
    return (
      <section className="section section--textures">
      <header className="section__header">
      <h2 className="section__title">Textures</h2>
      </header>
      <div className="section__body">
        <CardGrid>
          <Card cardImg="http://wwwhere.io/img/thumbs/subtlepatterns.jpg" cardURL="https://www.toptal.com/designers/subtlepatterns/" cardName="Subtle Patterns" cardDesc="Free textures for your next web project." imgHasBackground={true} />
          <Card cardImg="http://makerbook.net/wp-content/uploads/makerbook_thumbnail_patternico-100x100.gif" cardURL="http://patternico.com/" cardName="Patternico" cardDesc="Handy tool for making seamless icon patterns fast and easy without all that Photoshop wizardry." imgHasBackground={true} />
          <Card cardImg="http://themakers.io/tools/wp-content/uploads/2016/11/Capture-d%E2%80%99e%CC%81cran-2016-11-14-a%CC%80-09.53.29.png" cardURL="https://www.heropatterns.com/" cardName="Hero Patterns" cardDesc="A collection of repeatable SVG background patterns for you to use on your web projects." imgHasBackground={true} />
        </CardGrid>
      </div>
      </section>
    );
  }
}

export default Textures;



