import React, { Component } from 'react';
import logo from '../../logo.svg';
import '../../css/styles.css';
import featherIcons from '../../vendors/feather/feather-sprite.svg';
import jamIcons from '../../vendors/jam/jam-sprite.svg';
import {
  Route,
  withRouter,
  Link,
  NavLink,
  Switch,
  browserHistory
} from "react-router-dom";
import Photography from "./Photography";
import Graphics from "./Graphics";
import Colors from "./Colors";
import Textures from './Textures';
import Typography from './Typography';
import UserInterface from './UserInterface';


function NavIcon(props) {
  return (
    <svg className={`nav__icon jam jam-${props.name}`}
    xmlns= "http://www.w3.org/2000/svg"
    xmlnsXlink="http://www.w3.org/1999/xlink"
    viewBox='0 0 24 24'
    width="24"
    height="24"
    fill="currentColor"
    stroke="none"
    strokeWidth="2"
    strokeLinecap="round"
    strokeLinejoin="round">
      <use xlinkHref={`${jamIcons}#${props.name}`} />
    </svg>
  );
}

class Sidebar extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <div className='sidebar'>
        <nav className='sidebar__nav'>
        <div className="sidebar__logo"><Logo size="sm" /></div>
          <ul className='sidebar__list'>
            <li className='sidebar__item'><NavLink className="sidebar__link" to='/photography'><NavIcon name="camera" /><span class="sidebar__section-name">Photography</span></NavLink></li>
            <li className='sidebar__item'><NavLink className="sidebar__link" to='/graphics'><NavIcon name="brush" /><span class="sidebar__section-name">Graphics</span></NavLink></li>
            <li className='sidebar__item'><NavLink className="sidebar__link" to='/textures'><NavIcon name="ghost-org-square" /><span class="sidebar__section-name">Textures</span></NavLink></li>
            <li className='sidebar__item'><NavLink className="sidebar__link" to='/typography'><NavIcon name="background-color" /><span class="sidebar__section-name">Typography</span></NavLink></li>
            <li className='sidebar__item'><NavLink className="sidebar__link" to='/colors'><NavIcon name="eyedropper" /><span class="sidebar__section-name">Colors</span></NavLink></li>
            <li className='sidebar__item'><NavLink className="sidebar__link" to='/user-interface'><NavIcon name="layout" /><span class="sidebar__section-name">User Interface</span></NavLink></li>
          </ul>
        </nav>
      </div>
    );
  }
}

const Home = () => (
  <section className='section section--home'>
  <h1 onClick={this.handleClick} className="header__title"><Logo size='lg' /></h1>
    <NavGrid />
  </section>
);

const Logo = (props) => {
  return (
    <div className={`app-logo app-logo--${props.size} ${props.display}`}>
    <Link className="app-logo__link" to='/'>
    <svg className='feather feather-bookmark'
    xmlns= "http://www.w3.org/2000/svg"
    xmlnsXlink="http://www.w3.org/1999/xlink"
    viewBox='0 0 24 24'
    width='24'
    height='24'
    fill="none"
    stroke="currentColor"
    strokeWidth="2"
    strokeLinecap="round"
    strokeLinejoin="round">
      <use xlinkHref={`${featherIcons}#bookmark`} />
    </svg>
    <span className="app-name">Makerbook</span></Link>
    </div>
  );
}

function AppIcon(props) {
  return (
    <svg className={`app__icon feather feather-${props.name}`}
    xmlns= "http://www.w3.org/2000/svg"
    xmlnsXlink="http://www.w3.org/1999/xlink"
    viewBox='0 0 24 24'
    width="24"
    height="24"
    fill="none"
    stroke="currentColor"
    strokeWidth="2"
    strokeLinecap="round"
    strokeLinejoin="round">
      <use xlinkHref={`${featherIcons}#${props.name}`} />
    </svg>
  );
}

class SearchBar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isExpanded: false
    }
    this.handleClick = this.handleClick.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleClick(e) {
    e.stopPropagation();
    const searchFormInput = document.querySelector('.search-form__input');
    if (e.target.classList.contains('search-form__input') || searchFormInput.value !== '') return;
    this.setState(state => ({
      isExpanded: !state.isExpanded
    }));
  }

  handleSubmit(e) {
    e.preventDefault();
  }

  render() {
  return (
  <div onClick={(e) => this.handleClick(e)} className={`search-bar${this.state.isExpanded ? ' is-expanded' : ''}`}>
  <form className="search-form" onSubmit={(e) => this.handleSubmit(e)}>
    <input className="search-form__input" type="text" />
    <input className="search-form__submit" type="submit" value="" />
    <button type="button" className="btn search-form__btn"><AppIcon name="search" /></button>
</form>
  </div>
  );
  }
}

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      navIsExpanded: false
    };
    this.handleToggleClick = this.handleToggleClick.bind(this);
    this.handleClick = this.handleClick.bind(this);
  }

    handleToggleClick(e) {
      this.setState(state => ({
        navIsExpanded: !state.navIsExpanded
      }));
    }

    handleClick(e) {
      this.setState(state => ({
        navIsExpanded: false
      }));
      console.log(this.setState);
    }

  render() {
    return (
      <header className={`header${this.state.navIsExpanded && !this.props.hideNav ? ' expand-nav' : ''}${this.props.hideNav ? ' hide-nav':''}`}>
        <div className="sidebar__canvas">
        <button onClick={this.handleToggleClick} className={`sidebar__btn--toggle${this.props.hideToggle ? ' is-hidden' : ''}`}><SidebarIcon name="arrow-right" /></button>
        <Sidebar />
        </div>
        <div className="top-bar clearfix"><SearchBar /></div>
      </header>
    );
  }
}

function SidebarIcon(props) {
  return (
    <svg className={`sidebar__icon feather feather-${props.name}`}
    xmlns= "http://www.w3.org/2000/svg"
    xmlnsXlink="http://www.w3.org/1999/xlink"
    viewBox='0 0 24 24'
    width="24"
    height="24"
    fill="none"
    stroke="currentColor"
    strokeWidth="2"
    strokeLinecap="round"
    strokeLinejoin="round">
      <use xlinkHref={`${featherIcons}#${props.name}`} />
    </svg>
  );
}



function TileIcon(props) {
  return (
    <svg className={`tile__icon jam jam-${props.name}`}
    xmlns= "http://www.w3.org/2000/svg"
    xmlnsXlink="http://www.w3.org/1999/xlink"
    viewBox='0 0 24 24'
    width="24"
    height="24"
    fill="currentColor"
    stroke="none"
    strokeWidth="2"
    strokeLinecap="round"
    strokeLinejoin="round">
      <use xlinkHref={`${jamIcons}#${props.name}`} />
    </svg>
  );
}


class NavTile extends Component {
  constructor(props) {
    super(props);
  }
  
  render() {
    return (
      <div className='tile nav__tile'>
        <Link className='tile__link' to={this.props.tileURL}>
          <TileIcon className="tile__icon" name={this.props.tileIcon} />
          <span className='tile__name'>{this.props.tileName}</span>
        </Link>
      </div>
    );
  }
}

class NavGrid extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div className="nav-grid">
      <NavTile tileName='Photography' tileURL='/photography' tileIcon='camera' />
      <NavTile tileName='Graphics' tileURL='/graphics' tileIcon='brush' />
      <NavTile tileName='Textures' tileURL='/textures' tileIcon='ghost-org-square' />
      <NavTile tileName='Typography' tileURL='/typography' tileIcon='background-color' />
      <NavTile tileName='Colors' tileURL='/colors' tileIcon='eyedropper' />
      <NavTile tileName='User Interface' tileURL='/user-interface' tileIcon='layout' />
        </div>
    )
  }
}

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isHomePage: true
    };
    this.handlePageChange = this.handlePageChange.bind(this);
  }

  componentWillMount() {
    this.unlisten = this.props.history.listen((location, action) => {
     if (location.pathname === '/') {
       this.handlePageChange(true);
     } else {
       this.handlePageChange(false);
     }
    });
  }
  componentWillUnmount() {
      this.unlisten();
  }

handlePageChange(val) {
    this.setState({
      isHomePage: val
    });
  }

  render() {
    const {location} = this.props;
    console.log(this.state.isHomePage);

    return (
      <div className='app'>
        <Header hideToggle={this.state.isHomePage} hideNav={this.state.isHomePage} />
        <Switch location ={location}>
          <Route exact path="/" component={Home} />
          <Route path="/photography" component={Photography} />
          <Route path="/graphics" component={Graphics} />
          <Route path="/colors" component={Colors} />
          <Route path="/textures" component={Textures} />
          <Route path="/typography" component={Typography} />
          <Route path="/user-interface" component={UserInterface} />
        </Switch>
        </div>
      )
  }
}

export default withRouter(App);




