import React, { Component } from 'react';
import Card from "./Card";

class CardGrid extends Component {
  constructor(props) {
    super(props);
  }
  
  render() {
    return (
      <div className='card-grid'>
      {this.props.children}
      </div>
    );
  }
}

export default CardGrid;


