import React, { Component } from 'react';

class Card extends Component {
  constructor(props) {
    super(props);
  }
  
  render() {
    return (
      <div className='card'>
      <div className="card__header">
      <div className={`card__img-wrapper${this.props.imgHasBackground ? ' has-full-img' : ''}`}>
      <img className="card__img" src={this.props.cardImg} />
      </div>
      </div>
      <div className="card__body">
      <div className="card__title">
        <a className='card__link' href={this.props.cardURL}>
          <span className='card__name'>{this.props.cardName}</span>
        </a>
        </div>
        <p className="card__desc">{this.props.cardDesc}</p>
        </div>
      </div>
    );
  }
}

export default Card;


