import React, { Component } from "react";
import Card from "./Card";
import CardGrid from "./CardGrid";

class Photography extends Component {
  render() {
    return (
      <section className="section section--photography">
      <header className="section__header">
        <h2 className="section__title">Photography</h2>
        </header>
      <div className="section__body">
        <CardGrid>
          <Card cardImg="https://s3.amazonaws.com/heroku-www-files/customers/logos/unsplash.png" cardURL="https://unsplash.com/" cardName="Unsplash" cardDesc="Free (do whatever you want) high-resolution stock photos." imgHasBackground={false} />
          <Card cardImg="https://res-1.cloudinary.com/toolmuse/image/upload/v1491598093/production_Pixabay_lfjejo.png" cardURL="https://pixabay.com/" cardName="Pixabay" cardDesc="Over 410000 high quality photos, illustrations, and vector graphics. Free for commercial use. No attribution required." imgHasBackground={true} />
          <Card cardImg="https://res-5.cloudinary.com/toolmuse/image/upload/v1468332472/production_Logo_ve6qtv.png" cardURL="https://www.pexels.com/" cardName="Pexels" cardDesc="Free high quality photos you can use everywhere. Free for commercial use. No attribution required." imgHasBackground={false} />
        </CardGrid>
      </div>
      </section>
    );
  }
}

export default Photography;



