import React, { Component } from "react";
import Card from "./Card";
import CardGrid from "./CardGrid";
 
class Graphics extends Component {
  render() {
    return (
      <section className='section section--graphics'>
      <header className="section__header">
        <h2 className="section__title">Graphics</h2>
        </header>
        <div className="section__body">
        <CardGrid>
          <Card cardImg="https://upload.wikimedia.org/wikipedia/commons/thumb/8/85/The_Noun_Project_logo.svg/200px-The_Noun_Project_logo.svg.png" cardURL="https://thenounproject.com/" cardName="The Noun Project" cardDesc="Over a million curated icons, created by a global community" imgHasBackground={true} />
          <Card cardImg="http://www.comparatif-logiciels.fr/wp-content/uploads/2016/05/flaticon_comparatif_logiciels_saas-1.png" cardURL="https://www.flaticon.com/" cardName="FlatIcon" cardDesc="The largest database of free icons available in PNG, SVG, EPS, PSD and BASE 64 formats." imgHasBackground={true} />
          <Card cardImg="https://cdn0.iconfinder.com/static/img/favicons/favicon-194x194.png?bf2736d2f8" cardURL="https://www.iconfinder.com/" cardName="IconFinder" cardDesc="Search through 1,203,391 icons or browse 27,094 icon sets." imgHasBackground={false} />
        </CardGrid>
      </div>
      </section>
    );
  }
}
 
export default Graphics;


