import React, { Component } from 'react';
import Card from "./Card";
import CardGrid from "./CardGrid";


class Typography extends Component {
  render() {
    return (
      <section className="section section--typography">
      <header className="section__header">
        <h2 className="section__title">Typography</h2>
        </header>
        <div className="section__body">
        <CardGrid>
          <Card cardImg="https://pbs.twimg.com/profile_images/742732476213268480/ZWREQYdH_400x400.jpg" cardURL="https://fonts.google.com/" cardName="Google Fonts" cardDesc="Hundreds of free, open-source fonts optimized for the web. Just 3 quick steps between you and a good lookin’ website." imgHasBackground={true} />
          <Card cardImg="http://makerbook.net/wp-content/uploads/makerbook_thumbnail_font_squirrel-100x100.gif" cardURL="https://www.fontsquirrel.com/" cardName="Font Squirrel" cardDesc="Font Squirrel scours the internet in search of free, highest-quality, designer-friendly, commercial-use fonts and presents them for easy downloading." imgHasBackground={true} />
          <Card cardImg="http://makerbook.net/wp-content/uploads/makerbook_thumbnail_dafont-100x100.gif" cardURL="https://www.dafont.com/" cardName="daFont" cardDesc="Archive of freely downloadable fonts. Browse by alphabetical listing, by style, by author or by popularity." imgHasBackground={true} />
        </CardGrid>
      </div>
      </section>
    );
  }
}

export default Typography;



